.transactionHistory {
    width: 550px;
    margin: 0 auto;
    margin-top: 30px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
  }
  
  .tableHead {
    background-color: #00bcd5;
  }
  
  .row {
    padding: 10px 0;
  }
  
  .row:nth-child(even) {
    background-color: #d7e8ef;
  }
  
  .title {
    width: calc(100% / 3);
    padding: 10px 0;
    color: #ffffff;
    text-transform: uppercase;
  }
  
  .data {
    text-align: center;
    padding: 10px 0;
    text-transform: capitalize;
    color: #8d8989;
  }