.item {
    width: 250px;
    display: flex;
    align-items: center;
    padding: 5px 10px;
    border-radius: 5px;
    margin-bottom: 15px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
  }
  
  .status {
    display: block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-right: 10px;
  }
  
  .active {
    composes: status;
    background-color: #008000;
  }
  
  .disabled {
    composes: status;
    background-color: #ff0000;
  }
  
  .userAvatar {
    width: 60px;
    background-color: #9e9494;
    padding: 5px;
    border-radius: 5px;
  }
  
  .userName {
    font-size: 20px;
    font-weight: 500;
    margin-left: 10px;
  }