.statistics {
    margin: 0 auto;
    background-color: #ffffff;
    margin-top: 30px;
    width: 250px;
    border-radius: 5px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
  }
  
  .title {
    text-align: center;
    padding: 20px;
    color: #4c4c4c;
    background-color: #f9f7f7;
    text-transform: uppercase;
  }
  
  .statList {
    display: flex;
    list-style: none;
  }
  
  .item {
    display: flex;
    align-items: center;
    padding: 10px;
    flex-direction: column;
    letter-spacing: 0.03em;
    font-weight: 500;
    color: #ebeaea;
    text-shadow: 1px 1px #4c4c4c;
  }
  
  .label {
    font-size: 10px;
  }
  
  .percentage {
    font-size: 16px;
    margin-top: 5px;
  }